@import url(https://fonts.googleapis.com/css2?family=Eczar:wght@500;700&family=Roboto:wght@400;700;900&family=Source+Code+Pro:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;margin:0;padding:0;font-family:'Roboto', sans-serif;font-weight:400}h1,h2,h3,h4{font-family:'Eczar', serif;font-weight:500}h1{font-weight:700}textarea{resize:none}button{background:none;border:2px solid black;border-radius:3px;padding:5px 10px;color:black;transition:all 0.2s linear}button:hover{background:#2b2b2b;color:white;cursor:pointer}.app{width:100%;margin:0;padding:20px}header{padding:100px;text-align:center;width:100%;height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center}header h1{font-size:50px}header p{font-size:26px}.example{margin:0 0 200px;display:grid;grid-template:auto 400px / 350px 350px auto}.example h1,.example h2,.example h3,.example h4{font-family:'Roboto', sans-serif;font-weight:bold}.example__title{grid-column:1/4;padding:20px;text-align:center;font-size:30px}.example__input{height:auto;box-sizing:border-box;border:2px solid #1b1b1b;padding:20px;background:#1b1b1b;color:#d3d3d3;font-family:'Source Code Pro', monospace;line-height:1.6}.example__input:active,.example__input:focus{outline:0;border:2px solid #37a7e7}.example__input--css{grid-column:2/3}.example__input--html{grid-column:1/2;border-right:2px solid gray}.example__input::-webkit-scrollbar{background-color:#1b1b1b;width:16px}.example__input::-webkit-scrollbar-track{background-color:#1b1b1b}.example__input::-webkit-scrollbar-track:hover{background-color:#1b1b1b}.example__input::-webkit-scrollbar-thumb{background-color:#858586;border-radius:16px;border:5px solid #1b1b1b}.example__input::-webkit-scrollbar-thumb:hover{background-color:#a0a0a5}.example__input::-webkit-scrollbar-button{display:none}.example__output{grid-area:2 / 3 / 3 / 4;display:grid;place-items:center;width:100%;min-width:340px;height:auto;padding:20px;border:1px solid black;background:#d2e2eb;resize:both;overflow:hidden}footer{text-align:center}footer p,footer a{font-size:14px;font-style:italic;color:gray}

